import React from "react";
import "../css/workHistory.css";
import everyonicImg from "../static/everyonic.jfif";
import devMakersImg from "../static/devmakers.jfif";

const WorkHistory = () => {
  return (
    <section id="work-history">
      <hr />
      {/* <i class="fas fa-briefcase"></i> */}
      <h2>Experience</h2>
      <div className="work">
        <div className="work-main">
          <div className="work-item">
            <div className="work-item-info">
              <p className="work-date">
                <span>DEC 2021</span> - <span>Current</span>
              </p>
              <h3 className="work-title">Software Engineer / Consultant</h3>
              <p className="work-place">DevMakers</p>
              <p className="work-description">
              Contracted to assist the engineering team with creating an educational curriculum focusing on the professional development of software engineers looking to progress in their careers.
              </p>
            </div>
            <div className="work-img">
              <img
                src={devMakersImg}
                alt="DevMakers Logo"
              />
            </div>
          </div>
          <div className="work-item">
            <div className="work-item-info">
              <p className="work-date">
                <span>JAN 2021</span> - <span>JAN 2022</span>
              </p>
              <h3 className="work-title">Software Engineer</h3>
              <p className="work-place">Everyonic</p>
              <p className="work-description">
              Contracted to assist a team of engineers in the development of several projects. I created a customized admissions process for Golden West College’s online registration. Another project was a large full-scale online betting platform that allowed users to bet on competitive fantasy and real-world sports leagues.
              </p>
            </div>
            <div className="work-img">
              <img
                src={everyonicImg}
                alt="everyonic logo"
              />
            </div>
          </div>

          <div className="work-item">
            <div className="work-item-info">
              <p className="work-date">
                <span>AUG 2019</span> - <span>JAN 2020</span>
              </p>
              <h3 className="work-title">Information Developer</h3>
              <p className="work-place">Perspecta / City Of Anaheim</p>
              <p className="work-description">
                Assigned to the city of Anaheim’s applications and information
                development team to assist in the creation of an application to
                process and extract the data from tickets issued for use by
                attorneys and court officials. Additional tasks were to support
                business analysts along with maintenance and updating
                announcements from local city departments to reflect on City of
                Anaheim's website.
              </p>
            </div>
            <div className="work-img">
              <img
                src="data:image/jpeg;base64,"
                alt=""
              />
            </div>
          </div>
          <div className="work-item">
            <div className="work-item-info">
              <p className="work-date">
                <span>JUL 2019 </span> - <span>OCT 2019</span>
              </p>
              <h3 className="work-title">Assistant Instructor</h3>
              <p className="work-place">Redwood Code Academy</p>
              <p className="work-description">
                Assisted lead instructor with the execution of an evening
                Full-Stack continuing ed course while maintaining a challenging
                and informative learning environment in support of students'
                growth and learning.
              </p>
            </div>
            <div className="work-img">
              <img
                src="data:image/jpeg;base64,"
                alt="redwood code academy logo"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default WorkHistory;
